}
a.disabled {
    pointer-events: none;
}
.btn-primary {
    background-color: #1cb803 !important;
    border: 1px solid #ee2624 !important;
}
.Header-white {
    background-color: #fff;
}
.text-black {
    color: rgba(0, 0, 0, 0.822) !important;
}
